body {
  margin: 0;
  line-height: normal;
}

.popup-overlay {
  display: flex;
  flex-direction: column;
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
}

:root {
  /* fonts */
  --font-sf-pro-text: "SF Pro Text";
  --font-sen: Sen;
  --font-inter: Inter;
  --font-poppins: Poppins;
  --font-proxima-nova: "Proxima Nova";
  --font-sf-pro-display: "SF Pro Display";
  --title: Roobert-Regular;
  --font-outfit: Outfit;
  --font-inherit: inherit;

  /* font sizes */
  --font-size-6xl: 25px;
  --font-size-mini: 15px;
  --font-size-17xl: 36px;
  --font-size-smi: 13px;
  --font-size-mid: 17px;
  --font-size-lg: 18px;
  --font-size-base: 16px;
  --font-size-3xs: 10px;
  --font-size-sm: 14px;
  --font-size-xs: 12px;
  --font-size-xl: 20px;
  --title-size: 56px;
  --font-size-5xl: 24px;
  --font-size-13xl: 32px;

  /* Colors */
  --color-lightskyblue: #64d2ff;
  --basic-inactive-tab: #a3a3a3;
  --color-darkgray-100: rgba(157, 157, 157, 0.6);
  --white: #fff;
  --color-gray-100: #252529;
  --color-gray-200: #242424;
  --color-gray: #232323;
  --color-gray-300: rgba(35, 35, 35, 0.6);
  --color-gray-400: rgba(255, 255, 255, 0.3);
  --color-gainsboro: #e2e2e2;
  --color-darkslategray-100: #333;
  --color-dimgray: #707070;
  --color-dimgray-100: #6a515e;
  --color-black: #000;
  --accent-blue: #007aff;
  --color-silver-100: #c4c4c4;
  --color-cornflowerblue: #6cb5ff;
  --color-dodgerblue: #0a84ff;
  --opacity: rgba(235, 235, 245, 0.6);
  --color-crimson: #ff375f;
  --color-mediumseagreen: #22c96a;
  --color-honeydew: #e6fae6;
  --color-azure: #e6f8fa;

  /* Gaps */
  --gap-9xs: 4px;
  --gap-13xl: 32px;
  --gap-xs: 12px;
  --gap-45xl: 64px;

  /* Paddings */
  --padding-9xs: 4px;
  --padding-sm: 14px;
  --padding-6xs: 7px;
  --padding-lg: 18px;
  --padding-xl: 20px;
  --padding-13xl: 32px;
  --padding-base: 16px;
  --padding-8xl: 27px;

  /* border radiuses */
  --br-5xs: 8px;
  --br-xl: 20px;
  --br-mini: 15px;
  --br-10xs: 3px;
  --br-7xs: 6px;
  --br-12xl: 31px;
  --br-3xs: 10px;
  --br-31xl: 50px;
  --br-9xs: 4px;
}
